import React from 'react';
import {Avatar, Grid,Paper} from '@mui/material';

function Login() {
    const paperStyle={padding :20,height:'75vh', width:350, margin:"20px auto"}
    const avatarStyle={backgroundColor:'green'}
  return (
    <div className='bgm'>
    <Grid align='center'>
        <Paper elevation={10} style={paperStyle}>
            <Avatar style={avatarStyle}> <i class="fa fa-sign-in" aria-hidden="true"></i> </Avatar>
            <h2>Log In</h2>
            <div className='loginform'>
            <div className="inputBox">
                    <input type="text" name="" required="required"></input>
                    <span>E-mail</span>
                </div>
                <div className="inputBox">
                    <input type="password" name="" required="required"></input>
                    <span>Password</span>
                </div>
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                   <label class="form-check-label" for="flexCheckDefault">
                    Remember Me
                   </label>
                </div>
                <div className="inputBox">
                    <button type="Submit" name="" required="required" value="send">Submit</button>
                </div>
                <div class="forget">
                    <a href="../component/Home.jsx" >Forget password ?</a>
                </div>
                <div class="link">
                    <p>Do you have an not account ?<a href="../component/Home.jsx" >Sign Up</a></p>
                </div>
                </div>
        </Paper>
    </Grid>
    </div>
  )
}

export default Login;